.form-contact-section-home .inner-circles-wrap {
  border-radius: 50%;
  pointer-events: none;
  position: absolute;
}
.form-contact-section-home .inner-circle-base {
  border-radius: 50%;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
}
.form-contact-section-home .inner-circle-outer {
  height: 65.3%;
  width: 65.3%;
  z-index: 2;
}
.form-contact-section-home .inner-circle-core,
.form-contact-section-home .inner-circle-outer {
  backface-visibility: hidden;
  border-radius: 50%;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}
.form-contact-section-home .inner-circle-core {
  height: 25%;
  width: 25%;
  z-index: 3;
}
.form-contact-section-home .circle-full .inner-circle-base,
.form-contact-section-home .circle-full .inner-circle-core {
  background-color: #45fdd0;
}

