.block--u.quotes-light .quote-carousel .lSSlideOuter {
  overflow: visible;
}
.block--u.quotes-light .quote-carousel .lSSlideWrapper {
  border-radius: 12px;
  box-shadow: 3px 7px 7.5px 0px rgba(0, 0, 0, 0.1490196078);
}
.block--u.quotes-light .quote-carousel .slide {
  background-color: #fff;
}
.block--u.quotes-light .quote-carousel .slide .person-img-wrap .star--rating {
  display: none;
}
.block--u.quotes-light .quote-carousel .lSPager {
  display: none;
}
.block--u.quotes-light .quote-carousel .slide .txt-content {
  color: #002ea6;
}
.block--u.quotes-light .quote-carousel .slide .txt-content p {
  color: #002ea6;
}
.block--u.quotes-light .quote-carousel .slide .quote-by-txt,
.block--u.quotes-light .quote-carousel .slide .quote-by-info {
  position: relative;
  padding-left: 30px;
}
.block--u.quotes-light .quote-carousel .slide .quote-by-txt::before {
  content: "";
  position: absolute;
  left: -5px;
  top: 0px;
  height: 30px;
  width: 30px;
  background: url("/wp-content/themes/sekure/assets/media/icons/quote-left-mark-red.png") no-repeat center center;
}
.block--u.quotes-light .quote-carousel .slide .person-img-wrap img {
  border: none;
}
.block--u.quotes-light .quote-carousel .slider-navigation a {
  position: relative;
  height: 45px;
  width: 45px;
  background: #939598;
  border-radius: 50%;
  transform: none;
  transition: all 0.3s ease;
}
.block--u.quotes-light .quote-carousel .slider-navigation a:hover, .block--u.quotes-light .quote-carousel .slider-navigation a:focus {
  background: #002ea6;
}
.block--u.quotes-light .quote-carousel .slider-navigation a::before {
  content: "";
  position: absolute;
  top: 50%;
  width: 15px;
  height: 15px;
  border-right-color: #fff;
  border-top-color: #fff;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
  pointer-events: none;
  backface-visibility: hidden;
  transition: transform 0.3s cubic-bezier(0.61, 1, 0.88, 1);
  transform: translate3D(-50%, -50%, 0);
}
.block--u.quotes-light .quote-carousel .slider-navigation a.flex-prev::before {
  left: calc(50% + 4px);
  transform: translate3D(-50%, -50%, 0) rotate(45deg);
}
.block--u.quotes-light .quote-carousel .slider-navigation a.flex-next::before {
  left: calc(50% - 4px);
  transform: translate3D(-50%, -50%, 0) rotate(-135deg);
}

.list-check .txt-post-rtf ul,
.list-check .plain-txt-block ul {
