}
.list-check .txt-post-rtf ul li:before,
.list-check .plain-txt-block ul li:before {
  content: "";
  position: absolute;
  left: -8px !important;
  top: 5px !important;
  height: 17px !important;
  width: 17px !important;
  background-color: transparent !important;
  background: url("/wp-content/themes/sekure/assets/media/icons/GreenCheck.svg") no-repeat center center;
}
